import Vue from 'vue'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

Vue.directive('anchor', {
  inserted: function (el, binding) {
    el.onclick = function () {
      let selector = '#anchor-' + binding.value
      let obj = document.querySelector(selector)
      window.scrollTo({
        top: obj.offsetTop,
        behavior: "smooth"
      });
    }
  }
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
