<template>
  <div id="anchor-help" class="help dream" data-animate="fade,0.75s,ease-out">
    <div class="container">
      <h2>操作指引</h2>
      <div class="content">
        <div class="info">
          <div class="wrapper">
            <ul>
              <li v-for="item in guideList" :key="item.id">
                <img src="@/assets/help_1.jpg">
                <p>{{ item.content }}</p>
              </li>
            </ul>
          </div>
        </div>
        <div class="steps-list">
          <div id="backgroundHover"></div>
          <ul>
            <li
              v-for="item in guideList"
              :key="item.id"
              @click="selectedItem(item.id)"
              class="item"
            >{{ item.title }}</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { createHash } from "crypto";
export default {
  data() {
    return {
      guideList: [
        { id: 1, title: "切换收支", content: "点击钱袋子，切换收入/支出模式" },
        { id: 2, title: "分类排序", content: "长按分类即可进行拖动排序" },
        {
          id: 3,
          title: "图表操作",
          content: "点击钱袋子，切换收入/支出模式；点击日期，按年/月/日时间排序"
        },
        { id: 4, title: "日期切换", content: "点击日历，快速切换日期" },
        { id: 5, title: "更多操作", content: "滑动切换日期，下拉新增一笔消费" }
      ]
    };
  },
  mounted() {
    this.selectedItem(1);
  },
  methods: {
    selectedItem(itemId) {
      let liEles = document.querySelectorAll(".help .steps-list li");
      for (let i = 0; i < liEles.length; i++) {
        liEles[i].className = "item";
      }

      let selectedLiEle = liEles[itemId - 1];
      if (selectedLiEle === undefined) {
        return;
      }

      let bgHover = document.getElementById("backgroundHover");
      bgHover.style.top = selectedLiEle.offsetTop + "px";
      selectedLiEle.className = "item selected";

      let helpEles = document.querySelectorAll(".help .info li");
      for (let i = 0; i < helpEles.length; i++) {
        helpEles[i].className = "";
      }
      let selectedHelpEle = helpEles[itemId - 1];
      selectedHelpEle.className = "active";
    }
  }
};
</script>
<style>
#backgroundHover {
  position: absolute;
  background: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#77c0ea),
    to(#08c5b7)
  );
  background: linear-gradient(90deg, #77c0ea, #08c5b7);
  -webkit-box-shadow: 0 2px 15px 0 rgba(60, 60, 60, 0.12);
  box-shadow: 0 2px 15px 0 rgba(60, 60, 60, 0.12);
  border-radius: 12px;
  width: 100%;
  display: inline-block;
  top: 0;
  left: 0;
  height: 64px;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: top;
  transition-property: top;
}
.item {
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: color;
  transition-property: color;
  color: black;
}
li.selected {
  color: white;
}
</style>
